html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
}

#root,
.app-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.main-content {
  flex: 1;
}

.company {
  text-wrap: nowrap;
}
footer {
  background: black;
  color: white;
  text-align: center;
  padding: 10px;
  width: 100%;
  margin-top: auto;
}
a {
  color: white;
}
.footer {
  width: 100%;
  background-color: #333;
  color: white;
  text-align: center;
  padding: 15px 0;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}
@media screen and (max-width: 768px) {
  .nav-item {
    display: none;
  }
  p {
    text-wrap: nowrap;
  }
}
.nav-item {
  text-wrap: nowrap;
}
